import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { Board } from 'src/app/classes/board';

const types = ['played', 'wins', 'losses', 'rank', 'percentage']

@Component({
  selector: 'app-rival-match-board',
  templateUrl: './rival-match-board.component.html',
  styleUrls: ['./rival-match-board.component.sass']
})
export class RivalMatchBoardComponent implements OnInit, OnChanges {

  @Input() fData!: any
  @Input() rData!: any
  boards!: Board[]

  constructor() { }

  ngOnInit(): void {
  }

  ngOnChanges(changes: SimpleChanges) {

    if(changes){
      this.boards = []
      types.forEach(x => {

        let value = this.fData[x]
        let cValue = this.rData[x]
        x.charAt(0).toLocaleUpperCase()

        let board: Board = new Board(this.upperFirstChar(x), value, cValue);

        this.boards = [...this.boards, board]

      })

    }

  }

  upperFirstChar(data: string): string {

    let newValue: string = ''
    let isFirst = true;

    for (let char of data) {

      newValue += isFirst ? char.toUpperCase() : char
      isFirst = isFirst ? false : false

    }

    return newValue

  }

}
