export class Player {
  PAV_def!: string;
  year!: number;
  PAV_total_rank!: number;
  PAV_off!: string;
  mPAV_def!: string;
  pavid!: number;
  team!: number;
  PAV_mid!: string;
  PAV_mid_rank!: number;
  surname!: string;
  mPAV_rank!: number;
  PAV_def_rank!: number;
  mPAV_total!: string;
  PAV_total!: string;
  firstname!: string;
  PAV_off_rank!: number;
  mPAV_mid!: string;
  mPAV_off!: string;
  name!: string;
  games!: number;

  constructor(
    PAV_def: string,
    year: number,
    PAV_total_rank: number,
    PAV_off: string,
    mPAV_def: string,
    pavid: number,
    team: number,
    PAV_mid: string,
    PAV_mid_rank: number,
    surname: string,
    mPAV_rank: number,
    PAV_def_rank: number,
    mPAV_total: string,
    PAV_total: string,
    firstname: string,
    PAV_off_rank: number,
    mPAV_mid: string,
    mPAV_off: string,
    name: string,
    games: number
  ) {
    this.PAV_def = PAV_def;
    this.year = year;
    this.PAV_total_rank = PAV_total_rank;
    this.PAV_off = PAV_off;
    this.mPAV_def = mPAV_def;
    this.pavid = pavid;
    this.team = team;
    this.PAV_mid = PAV_mid;
    this.PAV_mid_rank = PAV_mid_rank;
    this.surname = surname;
    this.mPAV_rank = mPAV_rank;
    this.PAV_def_rank = PAV_def_rank;
    this.mPAV_total = mPAV_total;
    this.PAV_total = PAV_total;
    this.firstname = firstname;
    this.PAV_off_rank = PAV_off_rank;
    this.mPAV_mid = mPAV_mid;
    this.mPAV_off = mPAV_off;
    this.name = name;
    this.games = games;
  }
}
