import { Component, OnInit } from '@angular/core';
import { Game } from 'src/app/classes/game';
import { Team } from 'src/app/classes/team';
import { Tip } from 'src/app/classes/tip';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-view-league-table',
  templateUrl: './view-league-table.component.html',
  styleUrls: ['./view-league-table.component.sass']
})
export class ViewLeagueTableComponent implements OnInit {

  teams!: Team[];
  games!: Game[];
  tips!: Tip[];

  constructor(private dataService: DataService) { }

  ngOnInit(): void {
    this.getAFLTeams();
    this.getAFLGames();
    this.getAFLTips();
  }

  getAFLTeams() {
    this.dataService.getTeam().subscribe(temp => {this.teams = temp});
  }

  getAFLGames() {
    this.dataService.getGame().subscribe(temp => {

      temp.sort((firstEl: Game, secondEl: Game) => {
        
        let firstScore = this.getScoreofWinner(firstEl);
        let secondScore = this.getScoreofWinner(secondEl);
        return secondScore - firstScore;

      })

      this.games = temp;
    
    });
  }

  getScoreofWinner(data: Game): number {
    const { ateamid, hteamid, winnerteamid, ascore, hscore } = data;
    switch (winnerteamid)
    { 
      case hteamid:
        return hscore
      case ateamid:
        return ascore
      default:
        return ascore
    }
  }

  getAFLTips() {
    this.dataService.getTip().subscribe(temp => {this.tips = temp});
  }

}
