export class Standing {
  draws!: number;
  wins!: number;
  rank!: number;
  behinds_against!: number;
  played!: number;
  behinds_for!: number;
  against!: number;
  id!: number;
  losses!: number;
  percentage!: number;
  pts!: number;
  goals_for!: number;
  goals_against!: number;
  name!: string;

  constructor(
    draws: number,
    wins: number,
    rank: number,
    behinds_against: number,
    played: number,
    behinds_for: number,
    against: number,
    id: number,
    losses: number,
    percentage: number,
    pts: number,
    goals_for: number,
    goals_against: number,
    name: string
  ) {
    this.draws = draws;
    this.wins = wins;
    this.rank = rank;
    this.behinds_against = behinds_against;
    this.played = played;
    this.behinds_for = behinds_for;
    this.against = against;
    this.id = id;
    this.losses = losses;
    this.percentage = percentage;
    this.pts = pts;
    this.goals_for = goals_for;
    this.goals_against = goals_against;
    this.name = name;
  }
}
