import { Component, Input, OnChanges, OnDestroy, OnInit, SimpleChanges } from '@angular/core';
import { Game } from 'src/app/classes/game';
import { Player } from 'src/app/classes/player';
import { Team } from 'src/app/classes/team';
import { Tip } from 'src/app/classes/tip';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-team-result',
  templateUrl: './team-result.component.html',
  styleUrls: ['./team-result.component.sass']
})
export class TeamResultComponent implements OnInit, OnChanges, OnDestroy {

  isLoading: boolean = false;
  games!: Game[];
  venues!: string[]; 
  roundNum!: number;
  search: string = ''
  @Input() team!: Team;
  @Input() teams!: Team[];

  constructor(
    private dataService: DataService
  ) { }

  ngOnChanges(changes: SimpleChanges): void {
    if(changes['team']) {

      this.isLoading = true

      this.getGame();

    }
  }

  ngOnInit(): void {
  }

  getGame() {
    this.dataService.getGame().subscribe(temp => {

      let tempArr: Game[] = [];
      let currentYear: number = new Date().getFullYear();

      temp.forEach((item: Game) => {
        
        const { hteamid, ateamid, complete, year, round, id } = item;
        
        if(this.team && (hteamid == this.team.id || ateamid === this.team.id)) 
        {
          
          if (complete == 100 && year == currentYear) tempArr.push(item)
          else if (complete == 0) 
          { 
          
            item.setTip(this.getTip(id, year.toString(), round));
          
            tempArr.push(item)
          
          }
        }
      })

      this.games = tempArr
      this.isLoading = false
      this.roundNum = this.games.filter(game => game.complete == 100).length + 4

    })
  }

  getTip(game: number = 1, year: string = '2021', round: number = 20): Tip[] {

    let tips: Tip[] = []

    this.dataService.getTip(game, year, round).subscribe(temp => {
      
      temp.forEach((item: any) => {

        tips.push(item)
      
      })

    })

    return tips

  }

  getWonGame(): Game[] {
    
    return this.games.filter(x => x.complete == 100 && x.winnerteamid == this.team.id);
  
  }

  getItem(data: any) {

    console.log(this.search);

  }

  getPav(): Player[] {

    return this.team.player.filter(x => x.firstname.toLowerCase().includes(this.search.toLowerCase()) 
                                          || x.surname.toLowerCase().includes(this.search.toLowerCase()))

  }

  getTeamDefeated(data: Game): string {

    const { ateamid, hteam, ateam } = data

    return ateamid == this.team.id ? hteam : ateam;

  }

  getLogo(id: number): string {

    return this.teams.find(x => x.id == id)?.logo ?? '';

  }

  getDate(data: string): string {

    let date = new Date(data);

    return `${date.getDate()}-${date.getMonth()}`
  
  }
  
  ngOnDestroy(): void {

    console.log("finished work on component!")
  
  }

}
