import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { Game } from '../classes/game';
import { Tip } from '../classes/tip';
import { Team } from '../classes/team';
import { Standing } from '../classes/standing';
import { Player } from '../classes/player';

@Injectable({
  providedIn: 'root',
})
export class DataService {
  constructor(private http: HttpClient) {}

  getTeam() {
    return this.http
      .get('https://api.squiggle.com.au/?q=teams;year=2021')
      .pipe(
        map((data: any) =>
          data.teams.map(
            (item: any) => new Team(item.name, item.logo, item.id, item.abbrev)
          )
        )
      );
  }

  getGame() {
    return this.http
      .get('https://api.squiggle.com.au/?q=games;year=2021')
      .pipe(
        map((data: any) =>
          data.games.map(
            (item: any) =>
              new Game(
                item.hteamid,
                item.ateamid,
                item.hteam,
                item.ateam,
                item.winner,
                item.hscore,
                item.ascore,
                item.roundname,
                item.winnerteamid,
                item.complete,
                item.year,
                item.venue,
                item.localtime,
                item.date,
                item.id,
                item.round
              )
          )
        )
      );
  }

  getTip(game: number = 1, year: string = '2021', round: number = 20) {
    return this.http
      .get(
        `https://api.squiggle.com.au/?q=tips&game=${game}&year=${year}&round=${round}`
      )
      .pipe(
        map((data: any) =>
          data.tips.map(
            (item: any) =>
              new Tip(
                item.source,
                item.hteam,
                item.ateam,
                item.tip,
                item.hconfidence,
                item.margin
              )
          )
        )
      );
  }

  getPlayers() {
    return this.http
      .get(`https://api.squiggle.com.au/?q=pav`)
      .pipe(
        map((data: any) =>
          data.pav.map(
            (item: any) =>
              new Player(
                item.PAV_def,
                item.year,
                item.PAV_total_rank,
                item.PAV_off,
                item.mPAV_def,
                item.pavid,
                item.team,
                item.PAV_mid,
                item.PAV_mid_rank,
                item.surname,
                item.mPAV_rank,
                item.PAV_def_rank,
                item.mPAV_total,
                item.PAV_total,
                item.firstname,
                item.PAV_off_rank,
                item.mPAV_mid,
                item.mPAV_off,
                item.name,
                item.games
              )
          )
        )
      );
  }

  getStanding(year: string = '2021', round: number = 20) {
    return this.http
      .get(
        `https://api.squiggle.com.au/?q=standings&&year=${year}&round=${round}`
      )
      .pipe(
        map((data: any) =>
          data.standings.map(
            (item: any) =>
              new Standing(
                item.draws,
                item.wins,
                item.rank,
                item.behinds_against,
                item.played,
                item.behinds_for,
                item.against,
                item.id,
                item.losses,
                item.percentage,
                item.pts,
                item.goals_for,
                item.goals_against,
                item.name
              )
          )
        )
      );
  }
}
