import { Component, OnInit } from '@angular/core';
import { Game } from 'src/app/classes/game';
import { Standing } from 'src/app/classes/standing';
import { Team } from 'src/app/classes/team';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-rival-match',
  templateUrl: './rival-match.component.html',
  styleUrls: ['./rival-match.component.sass']
})
export class RivalMatchComponent implements OnInit {

  favouriteTeam!: Team
  rivalTeam!: Team
  teams!: Team[]
  games!: Game[]
  standings!: Standing[]
  isLoading: boolean = false
  fiveGame!: Game[]

  constructor(
    private dataService: DataService
  ) { }

  ngOnInit(): void {

    this.isLoading = true
    this.getAFLTeams()
    this.getGame()
  }

  getAFLTeams(): void {

    this.dataService.getTeam().subscribe(temp => { 
      
      this.teams = temp
      
    });
  
  }

  onSelect(data: Team, type: string): void {

    console.log(data)
    switch (type) {

      case 'favourite':

        this.favouriteTeam = data
        break

      case 'rival':
      default:

        this.rivalTeam = data
        break

    }

    if (this.favouriteTeam && this.rivalTeam)
      this.getFiveGame();

  }

  getFiveGame() {
    
    let numOfGame = 0
    this.fiveGame = []

    this.games.forEach(game => {

      const { hteamid, ateamid, complete } = game;

      if (numOfGame >= 5) return

      if (complete == 100 
        && ((hteamid == this.favouriteTeam.id && ateamid == this.rivalTeam.id) 
        || (ateamid == this.favouriteTeam.id && hteamid == this.rivalTeam.id))
      )
        {
          
          this.fiveGame = [... this.fiveGame, game]
          console.log(this.fiveGame)
          numOfGame++

        }

    })
    
  }

  gameWons = () => this.fiveGame.filter(x => x.winnerteamid == this.favouriteTeam.id).length

  gameDraw = () => this.fiveGame.filter(x => !x.winnerteamid).length

  gameLosses = () => this.fiveGame.filter(x => x.winnerteamid != this.favouriteTeam.id).length

  getGame() {

    this.dataService.getGame().subscribe((temp: Game[]) => {

      let year = new Date().getFullYear();

      let lastRound = temp.reverse().find(x => x.complete == 100 && x.year == year)?.round ?? 0;

      this.games = temp;

      this.getStanding(year.toString(), lastRound)

    })

  }

  getStanding(year: string = '2021', round: number = 20) {

    this.dataService.getStanding(year, round).subscribe((temp: any) => {
      
      temp.forEach((item: Standing) => {

        this.teams.forEach(team => {

          if (team.id == item.id) team.setStanding(item);
          
        })

      })

      this.isLoading = false

    })

  }

  disableFavourite(data: Team): boolean {

    return this.rivalTeam && this.rivalTeam.id == data.id

  }

  disableRival(data: Team): boolean {

    return this.favouriteTeam && this.favouriteTeam.id == data.id

  }

}
